@use 'assets/scss/normalize';
@use 'assets/scss/flex';

// @use 'swiper/swiper.scss';
// @use 'swiper/swiper-bundle.css';
// @use 'swiper/swiper-bundle.min.css';
// @use 'swiper/swiper.css';
@use 'swiper/swiper.min.css';
@use 'swiper/modules/navigation/navigation.min.css';

body {
    font-family: 'Mulish', sans-serif;
}

a,
img,
span {
    display: initial;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 20px 70px 20px 30px;
    margin-bottom: 30px;
    background-color: #fff;
}

.menu {
    &-button {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        width: 26px;
        height: 17px;
        margin-right: 25px;
    }
    &-line {
        width: 20px;
        height: 3px;
        background: #1f2022;
        border-radius: 1.5px;
    }
}

.logo {
    margin-right: 59px;
}

.input-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 830px;
    width: 50%;
    padding-right: 17px;
    background: #ebebeb;
    border-radius: 22px;
}

.input-search {
    padding: 12px 23px;
    background-color: transparent;
    border: none;
}

.button-search {
    width: 19px;
    height: 19px;

    background-color: transparent;
}

.icon {
    fill: #898989;
    &-search {
        fill: #000;
    }
    &-left,
    &-right {
        width: 28px;
        height: 28px;

        &_arrow {
            fill: #fff;
        }
    }
}

.icons {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.icons-link {
    opacity: 0.24;
}

.icons-link:hover {
    opacity: 0.7;
}

.icons-link:not(:last-child) {
    margin-right: 50px;
}

.user {
    margin-left: 45px;

    &-avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
}

.sidebar {
    position: fixed;
    top: 114px;
    left: 0;
    width: 12%;
    height: calc(100% - 114px);
    overflow-y: auto;
    padding-left: 30px;
    color: #898989;
    background-color: #fff;

    & .icon {
        width: 26px;
        height: 20px;
    }
}

.nav {
    &-menu {
        margin-bottom: 58px;
    }

    &-submenu {
        margin-bottom: 104px;

        &-title {
            color: #000000;
            font-size: 1.8rem;
            font-weight: bold;
            margin-left: 3px;
            margin-bottom: 30px;
        }
    }

    &-link {
        display: flex;
        align-items: center;
        margin-bottom: 28px;

        &:hover {
            color: #ff0000;

            & .icon {
                fill: #ff0000;
            }
        }

        &.setting-link {
            margin-bottom: 40px;
        }

        @at-root .nav-menu &:nth-child(3) {
            margin-bottom: 60px;
        }

        @at-root .nav-submenu & {
            margin-bottom: 23px;
        }
    }

    &-img {
        width: 26px;
        height: 26px;
        border-radius: 50%;
    }

    &-text {
        margin-left: 10px;
    }
}

// .main {}

.content {
    width: 100%;
    margin-top: 114px;
    padding-left: 13%;
}

.channel,
.recommended,
.food-drink {
    @extend %flex-sb-c;
    margin-bottom: 30px;

    &-title {
        display: flex;
        align-items: center;
        font-weight: bold;
        font-size: 26px;
        color: #19202c;
    }
    &-avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }

    &-text {
        margin: 0 20px;
    }
    &-subtext {
        color: #c2c2c2;
        margin-right: auto;
    }

    &-button-prev,
    &-button-next {
        height: 28px;
    }

    &-slider {
        margin-bottom: 50px;
    }
}

.slider-buttons {
    @extend %flex-sb-c;
    width: 66px;
    margin-right: 70px;
}

.video {
    &-card {
        width: 250px;

        @at-root .recommended-slider & {
            width: 540px;
        }
    }
    &-thumb {
        position: relative;
        width: 100%;
        height: 150px;
        border-radius: 14px;
        overflow: hidden;

        @at-root .recommended-slider & {
            height: 250px;
        }
    }
    &-thumbnail {
        object-position: center;
        object-fit: cover;
    }
    &-duration {
        position: absolute;
        bottom: 10px;
        right: 10px;
        padding: 3px 9px;
        font-size: 14px;
        line-height: 18px;
        color: #ffffff;
        background: #000000;
        mix-blend-mode: normal;
        opacity: 0.3;
        border-radius: 12px;
    }
    &-title {
        font-size: 16px;
        line-height: 20px;
        font-weight: bold;
        color: #19202c;
    }
    &-info {
        display: flex;
        justify-content: space-between;
        font-size: 13px;
        line-height: 16px;
        color: #c2c2c2;
    }
    &-counter {
    }
    &-views {
        &::after {
            content: '  ·  ';
        }
    }
    &-date {
    }
    &-channel {
    }
}
